@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';
.mobile-header-wrap {
  display: flex;
  justify-content: space-between;
  .mobile-connect-btn,
  .mobile-connected {
    padding: 5px;
    border-radius: 15px;
    font-size: 12px;
    line-height: 17px;
    font-weight: 600;
    cursor: pointer;
  }
  .mobile-connect-btn {
    border: 1px solid rgba($color: #ffffff, $alpha: 0.1);
    color: #ffffff;
    @include lightThemeOnly {
      background: #722fff;
      border: 1px solid #722fff;
    }
  }
  .mobile-connected {
    background: rgba(90, 20, 225, 0.2);
    border: 1px solid rgba(193, 160, 255, 0.31);
    backdrop-filter: blur(2px);
    color: #c1a0ff;
    @include lightThemeOnly {
      background: #722fff;
      border: 1px solid #722fff;
      color: #ffffff;
    }
    &-disabled {
      opacity: 0.3;
    }
  }
  .mobile-wallet-logo {
    img {
      height: 22px;
    }
  }
  .mobile-wallet-logo {
    border-radius: 50%;
    margin-right: 5px;
    width: 22px;
    height: 22px;
    img {
      border-radius: 50%;
    }
  }
  .mobile-header-right {
    position: relative;
  }
  .wallet-notice-tag {
    position: absolute;
    right: 0;
    top: -7px;
    transform: translateX(50%);
    padding: 0 3px;
    min-width: 14px;
    height: 14px;
    line-height: 14px;
    font-size: 10px;
    text-align: center;
    color: #fff;
    background: rgba(255, 83, 0, 1);
    border-radius: 16px;
    cursor: pointer;
  }
}
